.links {
    display: flex;
    width: 8rem;
    height: 4rem;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
    padding-bottom: 3rem;
    
  }
  
  .link {
    transition: 0.5s;
   
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
  }
  
  .link a {
    color: #6927FF;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
  
  .link:hover,
  .link:active {
    background-color: #6927FF;
    border-color: #6927FF;
  }
  
  .link a:hover {
    color: #fff;
  }
  